<transition name="fade"><test-finish-note v-if="finished" @reset="reset" :scores="scores" :maxScores="maxScores"></test-finish-note></transition><transition name="fade"> <container v-if="!quests"><br><h1>Подождите, тест загружается...</h1><br><icon class="text-primary" icon="svg-spinners:clock" width="60" height="60"></icon></container></transition><transition name="fade"> <form v-if="!finished &amp;&amp; quests"><br><section v-for="quest in quests"><quest-radio @submit="setUserAnswer" :quest="quest"></quest-radio></section><br><button class="btn btn-primary mb-2" type="submit" @click="submit">Проверить тест</button></form></transition>